<template>
  <div class="app">
    <div id="overlay" v-show="overlay">
      <div class="cv-spinner">
        <p><strong>Aguarde! Processando dados...</strong></p>
        <span class="spinner"></span>
      </div>
    </div>
    <img :src="logo" class="inicial" alt="Chama Church">
    <h1>CHAMA FAMILY<br><br>II Workshop Família Saudáveis: Quebrando Silêncio</h1>
    <div class="container">
      <h2>Realize a sua inscrição</h2>
      <div class="borda">
        <form id="form">
          <div class="container-fluid">
            <div class="d-flex flex-row flex-wrap justify-content-center align-items-center">
              <div class="form-group col-12">
                <label for="faixaetaria" style="margin: 5px;">Escolha sua sala do WORKSHOP conforme sua
                  idade:</label>
                <select id="grupo_id" v-model="grupo_id" class="form-control col-12 mb-2">
                  <option :value="null">Selecionar...</option>
                  <option value="1">Descomplicando o casamento: Casais </option>
                  <option value="2">Sou criança e fui criada para louvar! Crianças 4 a 6 anos </option>
                  <option value="3">Sou criança e fui criada para louvar! Crianças 7 a 11 anos </option>
                  <option value="4">Escolhas: como elas afetam o nosso futuro. Adolescentes (12 a 17 anos) </option>
                  <option value="5">O que significa ser um jovem cristão nos dias de hoje: Universidade, trabalho, namoro
                    e vida espiritual. Jovens</option>
                  <option value="6">Seu corpo é a felicidade ! Master - (Acima de 60 anos)</option>
                  <option value="7">É possível recomeçar! (divorsiados e viúvos)</option>
                </select>
                <span class="mt-2 pt-2">Obs: Todos estão convocados a participar da ultima palestra:<br/>
                  <strong>Quebrando silencio na família: Responsabilidades das famílias na educação digital: Da
                    superproteção à superexposição que afetam as famílias saudáveis. (Familias)
                  </strong>
                </span>
              </div>

              <div class="form-group col-12 ">
                <label for="Nome" style="margin: 5px;">Nome</label>
                <input type="text" class="form-control" v-model="nome" style="margin: 5px;" id="nome"
                  placeholder="Nome do participante..." required="">
              </div>
              <div class="form-group col-12 col-md-4 col-sm-12">
                <label for="horaculto" style="margin: 5px;">Sexo</label>
                <select id="sexo" class="form-control" v-model="sexo">
                  <option :value="null">Selecionar...</option>
                  <option value="1">Masculino</option>
                  <option value="2">Feminino</option>
                </select>
              </div>
              <div class=" form-group col-12 col-md-4 col-sm-12">
                <label for="Date" style="margin: 5px;">Idade</label>
                <input type="number" class="form-control" v-model="idade" style="margin-left: 5px;" id="idade"
                  aria-describedby="idade" placeholder="Sua Idade" required="">
              </div>
              <div class="form-group col-12 col-md-4 col-sm-12">
                <label for="horaculto" style="margin: 5px;">Estado Civil</label>
                <select id="estado_civil_id" class="form-control" v-model="estado_civil_id">
                  <option :value="null">Selecionar...</option>
                  <option value="1">Solteiro</option>
                  <option value="2">Casado</option>
                  <option value="3">Divorciado</option>
                  <option value="4">Viúvo</option>
                </select>
              </div>
              <div class="form-group col-12 col-md-6 col-sm-12 ">
                <label for="Nome" style="margin: 5px;">Email</label>
                <input type="text" class="form-control" style="margin: 5px;" v-model="email" id="email" name="email"
                  placeholder="exemplo@gmail.com" required="">
              </div>
              <div class=" form-group col-12 col-md-6 col-sm-12">
                <label for="Date" style="margin: 5px;">Telefone</label>
                <input type="text" class="form-control" style="margin-left: 5px;" v-model="celular"
                  placeholder="Ex.: (00) 00000-0000" data-mask="00000000000" maxlength="11" autocomplete="off"
                  id="celular">
              </div>
              <div class="form-group col-12">
                <label for="Nome" style="margin: 5px;">Qual é o Nome do Líder do GR que você participa?
                </label>
                <input type="text" class="form-control" style="margin: 5px;" v-model="nome_lider_gr" id="nome_lider_gr"
                  placeholder="Ex: Alisson e Dina..." required="">
              </div>
              <button type="button" @click="inscricao"
                class="btn btn-warning col-10 col-md-6 col-lg-6 col-sm-8 col-xl-6 d-flex justify-content-center "
                id="envio">Confirme sua inscrição</button>
            </div>
          </div>
        </form>
      </div>
      <div class="final">
        <img :src="logo" alt="Chama Church" class="imgprincipal-chamachurch">
      </div>
      <h3>CHAMA CHURCH - MINHA IGREJA, MINHA FAMÍLIA</h3>
      <footer class="d-flex flex-wrap justify-content-between">
        <div>
          <img
            src="data:image/svg+xml;base64,">
          <label>/Chama Church</label>
        </div>
        <div>
          <img
            src="data:image/svg+xml;base64,">
          <label>Chama Church</label>
        </div>
        <div>
          <img
            src="data:image/svg+xml;base64,">
          <label>92 98404-5474</label>
        </div>
        <div>
          <img
            src="data:image/svg+xml;base64,">
          <label>@chamachurch</label>
        </div>
      </footer>

    </div>
    <div id="part2">
      <label id="txt6"><i class="material-icons tiny"> copyright</i> {{ new Date().getFullYear() }} ChamaTech - All right
        reserved</label>
    </div>
    <template>
      <b-modal ref="success-modal" hide-footer hide-header centered>
        <div class="d-block text-center mt-2">
          Inscrição Realizada com sucesso!
        </div>
        <b-button class="mt-2" variant="outline-warning" block @click="toggleModal">Fechar</b-button>
      </b-modal>
    </template>
  </div>
</template>

<script>
import logo from '@/assets/logo.avif'
import Vue from 'vue'
import miniToastr from 'mini-toastr'

miniToastr.init()

export default {
  name: 'SiteTemplate',
  data () {
    return {
      logo,
      grupo_id: null,
      nome: '',
      sexo: null,
      idade: '',
      estado_civil_id: null,
      celular: '',
      email: '',
      nome_lider_gr: '',
      overlay: false
    }
  },
  methods: {
    inscricao () {
      this.overlay = true
      let data = {
        grupo_id: this.grupo_id,
        nome: this.nome,
        sexo: this.sexo,
        idade: this.idade,
        estado_civil_id: this.estado_civil_id,
        celular: this.celular,
        email: this.email,
        nome_lider_gr: this.nome_lider_gr
      }

      Vue.http.post(process.env.ROOT_API1 + 'api/cadastrar-workshop', data).then(response => {
        if (response.status === 200) {
          this.sucesso(response.body)
          this.$refs['success-modal'].show()
        } else {
          this.erro('Erro ao confirmar inscrição!')
          this.overlay = false
        }
        this.overlay = false
      }).catch(e => {
        if (e.status === 422) {
          let arrayDeValores = Object.values(e.body.errors)
          this.erro(arrayDeValores.shift())
          this.overlay = false
        } else { this.erro('Servidor está fora do ar!'); this.overlay = false }
      })
    },
    toggleModal () {
      this.$router.go(0)
    },
    navigate (route) {
      this.$router.push(route)
    },
    sucesso (mensagem) {
      this.$bvToast.toast(mensagem, {
        title: 'Sucesso',
        autoHideDelay: 10000,
        appendToast: true,
        variant: 'info'
      })
    },
    erro (mensagem) {
      this.$bvToast.toast(mensagem, {
        title: 'Erro',
        autoHideDelay: 10000,
        appendToast: true,
        variant: 'danger'
      })
    }
  }
}
</script>

<style lang="scss">
// CoreUI Icons Set
@import "@coreui/icons/css/coreui-icons.min.css";
/* Import Font Awesome Icons Set */
$fa-font-path: "~font-awesome/fonts/";
@import "~font-awesome/scss/font-awesome.scss";
/* Import Simple Line Icons Set */
$simple-line-font-path: "~simple-line-icons/fonts/";
@import "~simple-line-icons/scss/simple-line-icons.scss";
/* Import Flag Icons Set */
@import "flag-icon-css/css/flag-icon.min.css";
/* Import Bootstrap Vue Styles */
@import "bootstrap-vue/dist/bootstrap-vue.css";
// Import Main styles for this application
@import "../../assets/scss/style";
</style>

<style scoped>
.app {
  background-color: black !important;
  color: white !important;
}

.inicial {
  width: 120px;
  right: auto;
  height: 124px;
  display: block;
  margin: 10px auto;

}

.borda {
  background: #5e59515c;
  padding: 20px;
  border-radius: 50px;
}

h1 {
  color: rgb(255 131 0);
  text-align: center;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

h2 {
  color: white;
  text-align: center;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  margin: 10px auto !important;
}

label {
  font-weight: bold;
}

button {
  color: white !important;
  background-color: rgb(255 131 0) !important;
  border-color: rgb(255 131 0) !important;
  text-align: center;
  margin: 30px auto !important;
  font-weight: bold;
}

.final img {
  height: 40px;
  width: 40px;
  border-radius: 100%;
  display: block;
  margin: 30px auto;
}

h3 {
  font-size: 15px !important;
  text-align: center;
  color: rgb(255, 126, 51);
  margin-bottom: 20px;

}

footer {
  margin: 30px auto;
}

footer img {
  height: 40px;
  width: 40px;
  border-radius: 100%;

}

#part2 {
  color: rgb(255, 126, 51);
  width: 100%;
  background-color: rgb(39, 38, 38);
  position: relative;
  margin-top: 15px;
  font-size: 0.7rem;
  text-align: center;
  text-align: center;
}

#overlay {
  position: fixed;
  top: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.cv-spinner {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.cv-spinner p {
  color: white;
  font-weight: bold;
  font-size: large;
  z-index: 9999;
}

.spinner {
  width: 40px;
  height: 40px;
  border: 4px #ddd solid;
  border-top: 4px #2e93e6 solid;
  border-radius: 50%;
  animation: sp-anime 0.8s infinite linear;
}

@keyframes sp-anime {
  100% {
    transform: rotate(360deg);
  }
}
</style>



